.pricing{
  display: flex;
  flex-direction: column;
}
.one{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.two{
  background-color: black;
  color: white;
  padding: 40px;
  text-align: center;
  padding-bottom: 60px;
}
h1{
  font-size: 50px;
  padding-bottom: 0px;
  margin-bottom: 0px;
}
h3{
  color: gray;
  font-size: 23px;
  padding-top: 0px;
  margin-top: 0px;
  font-weight:normal;
}
.three{
  display: flex;
  flex-wrap:wrap;
  justify-content: space-around;
  margin: 25mm;
}
img{
  height: 40mm;
  width: 60mm;
  object-fit: cover;
  overflow: hidden;
}
.card{
  border: 1px solid rgb(184, 182, 182) ;
  margin-bottom: 10mm;
  text-align: center;
  padding-bottom: 20px;
}
.badge{
  background-color: black;
  color: white;
  position: absolute;
  padding: 1mm;
  border-radius: 00.5rem;
  margin: 2mm;
  
}
.price{
  text-decoration: line-through;
}
.btn{
  border: 1px solid black ;
  padding: 3mm;
  background-color: white;
  margin: 2mm;
  border-radius: 0.5rem;
}
.itemsincart{
  background-color: black;
  color: white;
  border-radius: 50%;
  padding: 1mm ;
}